<template>
  <el-container>
    <el-header>
      <div id="left">
        <span style="margin-right:30px;">Best Wu</span>
        <span style="margin-right:30px;">新浪微博：itwujt@sina.com</span>
        <span>微信：1227321179</span>
      </div>
    </el-header>
    <el-container>
      <el-aside>
        <!-- 导航 组件 -->
        <Navigation/>
      </el-aside>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  import Navigation from './Navigation'

  export default {
    name: 'Layout',
    components: {
      Navigation
    }
  }
</script>

<style lang="scss" scoped>
  .el-header {
    background-color: #151515;
    color: #B5E853;
    font-weight: bold;
    line-height: 60px;
  }
  
  .el-aside {
    background-color: #151515;
    color: #B5E853;
    font-weight: bold;
    text-align: left;
    height: calc(100vh - 60px);
    width: 300px;
  }
  
  .el-main {
    background-color: #151515;
    color: #B5E853;
    font-weight: bold;
  }
</style>


