import Vue from 'vue'
import Router from 'vue-router'

import Singleton from '@/components/designpatterns/Singleton'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/dp/singleton', // Design patterns
      name: 'Singleton',
      component: Singleton
    }
  ]
})



